import axios from 'axios'
import { Message } from 'view-ui-plus'

let basePath = localStorage.getItem('code-gen-base-path')

if (!basePath) {
  basePath = ''
}
// 创建 axios 实例
const service = axios.create({
  baseURL: basePath + '/codegen', // api base_url
  timeout: 6000, // 请求超时时间
})

const err = (error) => {
  if (error.response) {
    console.log(error.response)
  }
  Message.error('服务器请求异常')
  return Promise.reject(error)
}

// request interceptor
service.interceptors.request.use((config) => {
  return config
}, err)

/**
 * response interceptor
 * 所有请求统一返回
 */
service.interceptors.response.use((response) => {
  if (response.request.responseType === 'blob') {
    return response
  }

  console.log(response)

  let whiteUrlList = ['/tpl/list']

  if (response.data.code == 200 && response.config.method != 'get' && whiteUrlList.indexOf(response.config.url) == -1) {
    Message.success(response.data.message)
  }

  if (response.data.code == 500) {
    Message.error(response.data.message)
  }

  return response.data
}, err)

export default service
